<script>
  import ProfessionalGamesEn from '@/components/Helpers/ProfessionalGames/ProfessionalGamesEn.js';
  import ProfessionalGamesPt from '@/components/Helpers/ProfessionalGames/ProfessionalGamesPt.js';

  export default {
    data() {
      return {
        hoveredIndex: null,
        ProfessionalGames: null,
      };
    },
    mounted(){
      const userLanguage = navigator.language || navigator.userLanguage;

      if(userLanguage.startsWith("pt")){
        this.ProfessionalGames = ProfessionalGamesPt;
      }
      else{
        this.ProfessionalGames = ProfessionalGamesEn;
      }
    },
    methods: {
      getImage(img) {
        return require(`@/assets/games/${img.gameBanner}.png`);
      },
      showInfo(index) {
        this.hoveredIndex = index;
      },
      hideInfo() {
        this.hoveredIndex = null;
      },
    },
  };
</script>

