<template>
  <div class="page">
    <div class="page-container">
      <div class="title-container" id="contact">
        <img class="title-icon" src="@/assets/navbar/contact_icon.png" alt="Icon">
        <span class="title">{{ $t('contact.contact') }}</span>
      </div>
    </div>

    <div v-if=!emailSent> 
      <form @submit.prevent="sendEmail">
        <div class="form"> 
          <div class="form-background">
            <div class="name-email-container">
              <input class="text" type="text" id="name" name="name" :placeholder="$t('contact.name')" v-model="name">
              <input class="text" type="text" id="email" name="email" :placeholder="$t('contact.email')" v-model="email">
            </div>

            <div class="subject-container">
              <div class="subject">
                <input class="text" type="text" id="subject" name="subject" :placeholder="$t('contact.subject')" v-model="subject">
              </div>
            </div>

            <div class="message-container">
              <textarea class="text" type="text" id="message" name="message" :placeholder="$t('contact.message')" v-model="message"></textarea>
            </div>

            <div class="submit-container">
              <input class="submit" type="submit" value=" ">
                <div class="send-email">
                  <img src="@/assets/email.png" alt="Download" class="download-icon">
                  <span class="submit-text">{{ $t('contact.send-email') }}</span>
                </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  
    <div v-if=emailSent> 
      <div class="border-thanks-holder">
        <div class="border-thanks">
          <p class="thanks-text">{{ $t('contact.email-sent') }}</p>
        </div>
      </div>
    </div>

    <div class="icon-social-container">
      <a href="https://play.google.com/store/apps/developer?id=Black+Paladins" target="_blank"> 
      <img class="icon__social" src="@/assets/navbar/play_store_icon.png" alt="Play Store">
      </a>
      
      <a href="https://github.com/jerffesonj/" target="_blank"> 
      <img class="icon__social" src="@/assets/navbar/github_icon.png" alt="Github">
      </a>
      
      <a href="https://www.linkedin.com/in/jerffesonj/" target="_blank"> 
      <img class="icon__social" src="@/assets/navbar/linkedin_icon.png" alt="Icon">
      </a>
      
      <a href="https://jerffesonj.itch.io/" target="_blank"> 
      <img class="icon__social" src="@/assets/navbar/itch_icon.png" alt="itch.io">
      </a>
    </div>
  
    <div class="rights">
      <span class="rights-text" v-text="` ${getCurrentYear()} ${$t('contact.rights')}`"></span>

    </div>
  </div>
  
</template>
  
<script>
  import emailjs from 'emailjs-com';
  
  export default {
    name: 'ContactUs',
    data() {
      return {
        name: '',
        subject: '',
        email: '',
        message: '',
        emailSent: false,
      }
    },
    methods: {
      sendEmail(e) {

        if(this.name == '' || this.subject == '' || this.email == '' || this.message == ''){
          return;
        }

        try {
          emailjs.sendForm(
            'gmail_service', 
            'gmail_template', 
            e.target,
            'ZaZa9cha-AK-qdFKD', {
              name: this.name,
              subject: this.subject,
              email: this.email,
              message: this.message
            }
          )
        } catch(error) {
            console.log({error})
        }

        // Reset form field
        this.name = ''
        this.subject = ''
        this.email = ''
        this.message = ''

        this.emailSent = true;
      },
      getCurrentYear(){
        return new Date().getFullYear();
      }
    }
  }
  
</script>

<style scoped>
  .thanks-text{
    font-family: 'Poppins-Regular';
