  }
  .submit-container{
    display: flex;
    justify-content: center;
    margin-top: 25px;
    margin-bottom: 25px;
  }
  .subject{
    display: flex;
  }
  .text{
      font-family: 'Poppins-Light';
      color: C6C6C5;
      font-size: 15px;
  }
  .submit-text{
    font-family: 'Poppins-Bold';
    color: white;
