    display: inline-block;
    border: 1px solid #C6C6C5;
    border-radius: 15px;
    box-sizing: border-box;
  }
  
  .submit {
    display: flex;
    padding: 0px;
    background-color: #4787FC; /* Cor de fundo do boto */
    color: white; /* Cor do texto do boto */
    text-decoration: none;
    border-radius: 20px;
    transition: background-color 0.3s ease;
    width: 240px;
